.MainContainer {
  perspective: 1px;
  transform-style: preserve-3d;
  height: 100vh;
  overflow-x: hidden;
  overflow-y: scroll;
}

.ParallaxContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 0 auto;
      flex: 1 0 auto;
  position: relative;
  transform: translateZ(-1px) scale(2) translateY(-60px);
  z-index: -1;
  left: -8px;
}
@media screen and (max-device-width: 960px) {
  .desktop-only {
    display: none;
  }
}

@media screen and (min-device-width: 961px) {
  .mobile-only {
    display: none;
  }
}
html, body {
  height: 100%;
  width: 100%;
  font-size: 1.05em;
  font-family: 'Roboto', sans-serif;
  text-align: center;
  font-weight: 300;
  color: #333;
  background: #FEFBF5;
}

p {
  margin: 0;
}

a {
  text-decoration: none;
  color: #333;
}

li {
  list-style: none;
  text-align: left;
  padding-left: 5px;
  text-indent: -5px;
}

li.list-minus::before {
  content: " - ";
  padding-right: 10px;
}

li.list-plus::before {
  content: "+";
  padding-right: 10px;
}

button {
  display: inline-block;
  color: #FEFBF5;
  font-weight: 300;
  border: 1px solid;
  border-color: #333;
  padding: 5px 10px;
  background: #5E002544;
  transition: background-color 0.2s ease-in-out, border-color 0.2s ease-in-out;
}

button:hover {
  cursor: pointer;
  background: #5E0025FF;
  border-color: #FEFBF5;
  transition: background-color 0.2s ease-in-out, border-color 0.2s ease-in-out;
}

.breaker {
  display: block;
  width: 100vw;
  height: 1px;
}

.emphasis {
  font-weight: 400;
  color: #2E74B5;
}

.icon {
  display: inline-block;
}

.link {
  transition: color 0.2s ease-in-out;
  position: relative;
}

.link.inverted::after {
  background: #2E74B5;
}

.link.inverted:hover {
  color: #2E74B5;
}

.link::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  height: 2px;
  background: #2E74B5;
  width: 0;
  transition: width 0.2s ease-in-out;
}

.link:hover {
  color: #2E74B5;
  transition: color 0.2s ease-in-out;
}

.link:hover::after {
  width: 100%;
  transition: width 0.2s ease-in-out;
}

.paragraph {
  text-align: justify;
}

.paragraph:not(:last-child) {
  margin-bottom: 25px;
}

.round-title-block {
  position: absolute;
  text-align: left;
  font-size: 2em;
  padding: 8px 0 8px 30px;
  border-top-left-radius: 50px;
  border-bottom-left-radius: 50px;
  width: 35%;
  right: 0;
}

.nav-list, .header-block, .about-me-block, .my-projects-block, .contact-block {
  margin: 0 auto;
  max-width: 1080px;
  width: 1080px;
  display: block;
}

.body-block {
  padding: 50px 0;
}

.navbar {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 5;
  width: 100%;
  background: #5E0025;
}

.navbar.inverted {
  background: #2E74B5;
}

.nav-list {
  display: -ms-flexbox;
  display: flex;
  padding: 20px 50px;
  text-align: left;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.nav-list-object {
  display: inline-block;
  font-size: 1.2em;
  color: #FEFBF5;
}

.nav-list-object a {
  color: #FEFBF5;
}

.header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  height: 600px;
  background: url(bg7.31c1c9c2f46c1cc95c79.png) 0 10%/100%;
  position: relative;
}

.header::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.header-block {
  border-left: 3px #5E0025 solid;
  color: #FEFBF5;
  text-align: left;
}

.header-content {
  position: relative;
  z-index: 1;
  margin: 5px 0 5px 40px;
}

.header-content.header-title {
  font-size: 3em;
}

.about-me {
  background: #FEFBF5;
}

.about-me-block {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row nowrap;
      flex-flow: row nowrap;
}

.about-me-title-block {
  color: #FEFBF5;
  background: #2E74B5;
}

.about-me-image, .about-me-text {
  -ms-flex: 1 0 50%;
      flex: 1 0 50%;
}

.about-me-content, .about-me-image {
  padding-top: 80px;
}

.about-me-image {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  -ms-flex-pack: center;
      justify-content: center;
}

.about-me-image .image {
  width: 50%;
  border-radius: 100%;
  background: #5E0025EE;
  box-shadow: -50px -50px 0px 0px #5E0025EE;
  -ms-flex-item-align: center;
      -ms-grid-row-align: center;
      align-self: center;
}

.my-projects {
  background: #2E74B5;
  color: #FEFBF5;
}

.project-title {
  font-size: 3em;
  margin-bottom: 50px;
  position: relative;
}

.project-title::before {
  content: "";
  position: absolute;
  width: 35%;
  height: 1px;
  background: #FEFBF5;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}

.project-title::after {
  content: "";
  position: absolute;
  width: 35%;
  height: 1px;
  background: #FEFBF5;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
}

.projects-grid {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.project-item {
  position: relative;
  max-height: 350px;
  overflow: hidden;
  margin-bottom: 20px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row nowrap;
      flex-flow: row nowrap;
  -ms-flex-pack: center;
      justify-content: center;
  color: #FEFBF5;
}

.project-item .title {
  position: absolute;
  padding: 30px;
  top: 0;
  right: 0;
  left: 0;
  z-index: 2;
  font-size: 2em;
  font-weight: 400;
  text-align: left;
  transition: color 0.2s;
}

.project-item .title::after {
  content: "";
  width: 0;
  height: 2px;
  background: #FEFBF5;
  display: block;
  margin-top: 20px;
  transition: width 0.2s ease-in-out;
}

.project-item img {
  max-height: 100%;
  -ms-flex-item-align: center;
      -ms-grid-row-align: center;
      align-self: center;
  transform: scale(1.1);
  transition: transform 0.2s ease-in-out;
}

.project-item:hover {
  cursor: pointer;
}

.project-item:hover img {
  transform: scale(1.1) translateX(10px);
  transition: transform 0.2s ease-in-out;
}

.project-item:hover .project-text {
  opacity: 1;
  transition: opacity 0.2s ease-in-out;
}

.project-item:hover .title {
  color: #FEFBF5;
  transition: color 0.2s;
}

.project-item:hover .title::after {
  width: 100%;
  transition: width 0.2s ease-in-out;
}

.project-item.item-rect {
  width: 60%;
}

.project-item.item-square {
  width: 33%;
}

.project-item.item-fill {
  width: 2%;
  background: #FEFBF5;
  cursor: initial;
}

.project-text {
  color: #FEFBF5;
  text-align: left;
  position: absolute;
  right: 0;
  bottom: 0;
  top: 0;
  left: 0;
  z-index: 1;
  padding: 30px;
  padding-top: 120px;
  background: #5E0025EE;
  opacity: 0;
  transition: opacity 0.2s ease-in-out;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column nowrap;
      flex-flow: column nowrap;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.project-caption {
  font-style: italic;
  position: absolute;
  top: 70px;
  font-size: 0.8em;
}

.contact {
  background: #5E0025;
}

.contact-block {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: first baseline;
      align-items: first baseline;
  color: #FEFBF5;
}

.contact-title-block {
  background: #FEFBF5;
  color: #2E74B5;
}

.contact-infos, .contact-text {
  text-align: justify;
  -ms-flex: 1 0 50%;
      flex: 1 0 50%;
}

.contact-info {
  margin-bottom: 7px;
}

.contact-icon {
  height: 1.05em;
  margin-right: 7px;
}

.contact-content {
  padding-top: 80px;
  text-align: left;
}

.project-desc {
  background: #FEFBF5;
}

.project-desc-block {
  margin: 0 auto;
  max-width: 1080px;
  width: 1080px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
}

.project-desc-title-block {
  color: #FEFBF5;
  background: #2E74B5;
}

.project-skills-title-block {
  width: 100%;
  margin-bottom: -80px;
  text-align: center;
  color: #2E74B5;
}

.project-skills-title-block .project-title::before,
.project-skills-title-block .project-title::after {
  width: 35%;
  background: #2E74B5;
}

.project-desc-image, .project-desc-text {
  -ms-flex: 1 0 50%;
      flex: 1 0 50%;
}

.project-desc-content, .project-desc-image {
  padding-top: 80px;
}

.project-desc-image {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  -ms-flex-pack: center;
      justify-content: center;
}

.project-desc-image.top {
  -ms-flex-pack: start;
      justify-content: flex-start;
}

.project-desc-image .image {
  width: 90%;
  background: transparent;
  -ms-flex-item-align: center;
      -ms-grid-row-align: center;
      align-self: center;
}

.project-desc-image .image.left {
  -ms-flex-item-align: start;
      align-self: flex-start;
}

.project-desc-image .image.right {
  -ms-flex-item-align: end;
      align-self: flex-end;
}

.header-panem {
  background: url(panem-bg.76ff342829f7c53498bb.jpg) 0 0%/100%;
}
